.itemsWrapper {
	width: 100%;
	display: flex;
	flex-direction: column;
	row-gap: 16px;
}

@media screen and (min-width: 768px) {
	.itemsWrapper {
		padding-top: 8px;
		row-gap: 36px;
	}
}

@media screen and (min-width: 1280px) {
	.itemsWrapper {
		padding-top: 0px;
		row-gap: 48px;
	}
}